.header-container .navbar {
  padding: 0;
}
.header-container .navbar .container-fluid {
  padding: 1rem;
  background-color: var(--gray-bg);
  border-bottom: 1px solid var(--gray-border-line);
}
.header-container .navbar .container-fluid .navbar-brand img {
  width: 5.5rem;
}
.header-container .navbar ul {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.header-container .navbar ul li {
  width: 100%;
}
.header-container .navbar ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 100%;
  height: 4.5rem;
  position: relative;
  color: var(--gray-title);
  line-height: 4.5rem;
  font-weight: 700;
  transition: 0.2s;
}
.header-container .navbar ul li a:hover {
  color: var(--red);
}
.header-container .navbar ul li a.active {
  color: var(--red);
}
.header-container .navbar ul li a.active::after {
  content: "";
  height: 5px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: var(--red);
}
.header-container .navbar-toggler {
  border: none;
  transition: 0.2s;
}
.header-container .navbar-toggler:hover {
  background: var(--gray-200);
}
.header-container .navbar-toggler:focus {
  box-shadow: none;
}

.modal {
  max-width: 270px;
}
.modal .modal-content {
  max-width: 270px;
}
.modal .modal-content .modal-body,
.modal .modal-content .list-group-item {
  padding: 0;
}
.modal .modal-content .list-group-item {
  border: none;
  padding: 1rem 2rem;
}
.modal .modal-content .list-group-item:first-child {
  background-color: var(--gray-bg);
}
.modal .modal-content .list-group-item a {
  display: grid;
  grid-template-columns: 1fr 5fr;
  align-items: center;
  padding: 0.5rem 0;
  color: var(--gray-200);
  width: 100%;
  font-weight: 700;
  text-align: left;
  transition: 0.2s;
}
.modal .modal-content .list-group-item a:hover {
  color: var(--red);
}
.modal .modal-content .list-group-item a:active {
  color: var(--red);
}
.modal .modal-content .list-group-item a i {
  font-size: 1.4rem;
}

/* main */
main {
  padding: 2rem 0;
}
main .banner-carousel {
  position: relative;
}
main .banner-carousel .images {
  display: flex;
}
main .banner-carousel .images img {
  border-radius: 5px;
}
main .banner-carousel .images .slick-slide {
  margin: 1rem;
}
main .banner-carousel .images .slick-center {
  transform: scale(1.2);
}
main .sliders {
  opacity: 0.7;
}
main .sliders #arrow-prev,
main .sliders #arrow-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
}
main .sliders #arrow-prev {
  left: 1rem;
}
main .sliders #arrow-next {
  right: 1rem;
}
main .catalog-container {
  margin-top: 4rem;
  padding-left: 1rem;
}
main .catalog-container .catalog-content h5 {
  color: var(--black);
  margin-bottom: 1rem;
}
main .catalog-container .catalog-content .movie {
  position: relative;
  margin-right: 1rem;
  max-height: 250px;
  max-width: 140px;
}
main .catalog-container .catalog-content .movie button {
  position: absolute;
  top: 3%;
  right: 3%;
  height: 25px;
  width: 25px;
  padding: 5px;
  border: 2px solid var(--white);
  border-radius: 50%;
  font-size: 0.7rem;
  background: var(--black);
  color: var(--white);
  transition: 0.2s;
}
main .catalog-container .catalog-content .movie button:hover {
  background: var(--white);
  color: var(--black);
}
main .catalog-container .catalog-content .movie img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

/* footer */
.footer-container {
  background: var(--gray-bg);
}
.footer-container .socials-container,
.footer-container .minha-sky-container,
.footer-container .sky-empresas-container,
.footer-container .links-container,
.footer-container .rights-container {
  padding: 1rem 1rem;
}
.footer-container .socials-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid var(--gray-border-line);
  border-bottom: 1px solid var(--gray-border-line);
}
.footer-container .socials-container img {
  width: 5.5rem;
}
.footer-container .socials-container .socials-icons {
  color: var(--gray-title);
}
.footer-container .socials-container .socials-icons a + a {
  margin-left: 0.5rem;
}
.footer-container .minha-sky-container {
  display: flex;
  align-items: center;
  padding: 1.5rem 1rem;
}
.footer-container .minha-sky-container img {
  width: 3rem;
  margin-right: 1rem;
}
.footer-container .minha-sky-container p {
  margin: 0;
  color: var(--red);
  font-weight: 700;
  font-size: 1.2rem;
}
.footer-container a {
  transition: color 0.2s;
}
.footer-container a:hover {
  color: var(--red);
}
.footer-container .accordion,
.footer-container .accordion-button,
.footer-container .accordion-body {
  background: var(--gray-bg);
}
.footer-container .accordion-button,
.footer-container .sky-empresas-container {
  color: var(--black);
}
.footer-container .accordion {
  font-weight: 700;
  color: var(--gray-title);
}
.footer-container .accordion .accordion-button,
.footer-container .accordion .accordion-body {
  padding: 1rem;
}
.footer-container .accordion ul {
  padding: 0;
}
.footer-container .accordion ul li + li {
  margin-top: 1rem;
}
.footer-container .links-container {
  background: var(--white);
}
.footer-container .links-container a {
  color: var(--gray-title);
  line-height: 2.5rem;
  font-weight: 700;
  transition: color 0.2s;
}
.footer-container .links-container a:hover {
  color: var(--red);
}
.footer-container .rights-container p {
  margin: 0;
  color: var(--gray-title);
  text-align: center;
}